<template>
  <div id="app">
    <input type="date" @touchstart="show = true" v-model="startDate" readonly>
    -
    <input type="date" @touchstart="show = true" v-model="endDate" readonly>
    <!-- <DatePicker :displayRangeStart="start"/> -->
    <!-- <DatePicker /> -->
    <!-- <DatePicker reverseSelect/> -->
    <aki-date
      :restday="restday"
      :workday="workday"
      :display="show"
      :custom="data"
      @select="select"
      @cancel="cancel"
      @selectDisabled="selectErr"
      @viewport="viewport"
      autoComplete/>
  </div>
</template>

<script>
import DatePicker from './datepicker/DatePicker'
import { Message } from 'element-ui'

export default {
  components: {
    DatePicker
  },
  data() {
    return {
      startDate: '',
      endDate: '',
      restday: [],
      workday: ['2018-9-29', '2018-9-30'],
      data: ['￥1300', '￥1345', { highlight: true, text: '￥999' }],
      show: false
    }
  },
  methods: {
    select({ start, end, range }) {
      this.startDate = start.odate
      this.endDate = end.odate
      console.log('range', range)
      this.show = false
    },
    cancel() {
      this.show = false
    },
    selectErr(date) {
      console.log('date', date)
      Message({
        message: `无法选择该范围：${date.date}为${date.custom.dec}`,
        type: 'error',
        duration: 10000
      })
    },
    viewport({ year, month, date }) {
      console.log('现在出现在视线内的是：', `${year}-${month}`)
    }
  },
  created() {
    setTimeout(() => {
      this.workday = ['2018-8-25', '2018-8-26', '2018-8-27']
      this.restday = ['2018-8-22', '2018-8-23', '2018-8-24']
      this.data = [
        '￥1999',
        { text: '￥2018', bgcolor: 'lightgreen' },
        '', '', '', '',
        { highlight: true, text: '￥666', bgcolor: 'gold' },
        { highlight: true, text: '￥888' }, '', '',
        { disabled: true, bgcolor: 'red', dec: '正在维修' }
      ]
    setTimeout(() => {
      this.workday = ['2018-8-25', '2018-8-26', '2018-8-27']
      this.restday = ['2018-8-22', '2018-8-23', '2018-8-24']
      this.data = [
        '￥1922',
        { text: '￥2018', bgcolor: 'lightgreen' },
        '', '', '',
        { highlight: true, text: '￥666', bgcolor: 'gold' },
        { highlight: true, text: '￥888' }, '', '',
        { disabled: true, bgcolor: 'red', dec: '正在维修' }
      ]
    }, 2000);
    }, 2000);
  }
}
</script>

